.preloader {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  transition: all 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.preloader_loader,
.preloader__percents {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.preloader__percents {
  font-family: Arial, Helvetica, sans-serif;
  color: red;
  font-size: 20px;
  font-weight: bold;
}
.preloader--hide {
  display: none;
  visibility: hidden;
  z-index: -1;
}

